import { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { registerUser } from "../services/actions/auth-actions";
import { Input, PasswordInput, Button } from "@ya.praktikum/react-developer-burger-ui-components";

import styles from "./forms.module.css";

const RegistrationPage = () => {
  const dispatch = useDispatch();

  const [userData, setUserData] = useState({
    email: '',
    password: '',
    name: '',
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(registerUser(userData));
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className={styles.container}>
      <h1 className='mb-6 text text_type_main-medium'></h1>
      <form onSubmit={handleSubmit} className={`mb-20 ${styles.form}`}>
        <div className={`mb-6 ${styles.input}`}>
          <Input
            type={'text'}
            placeholder={''}
            onChange={handleChange}
            value={userData.name}
            error={false}
            errorText={''}
            name={'name'}
          />
        </div>
        <div className={`mb-6 ${styles.input}`}>
          <Input
            type={'email'}
            placeholder={'E-mail'}
            onChange={handleChange}
            value={userData.email}
            error={false}
            errorText={''}
            name={'email'}
          />
        </div>
        <div className={`mb-6 ${styles.input}`}>
          <PasswordInput
            onChange={handleChange}
            value={userData.password}
            name={'password'}
          />
        </div>
        <Button type='primary' size='medium'>
          
        </Button>
      </form>
      <p className='mb-4 text text_type_main-default text_color_inactive'>
         ?{' '}
        <Link to='/login' className={styles.link}>
          
        </Link>
      </p>
    </div>
  );
};

export default RegistrationPage;
